*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primery-bgc: #f6f6f6;
  --color-white: #fff;
  --auth-text-color: #1116;
  --text-color: #1119;
  --color-black: #111;
  --description-color: #b4afaf;
  --color-violet: #4f2ee8;
  --color-yellow: #eac645;
  --color-wrapper: #9d9d9d;
  --color-switcher: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  --amazon-filter-img: grayscale(99%);
  --amazon-filter-wImg: grayscale(99%);
  --amazon-hover: grayscale(none);
  --amazon-hover: grayscale(none);
  --filter-apple-img: grayscale(99%);
  --filter-apple-wImg: grayscale(99%);
  --filter-img: grayscale(99%);
  --filter-wImg: grayscale(99%);
  --color-accent: #4f2ee8;
  --transition__stock: all 300ms linear;
  --scroll-color: #e0e0e0;
}

:root body.dark-theme {
  --primery-bgc: #202024;
  --color-white: #111;
  --color-black: #fff;
  --color-accent: #eac645;
  --color-switcher: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
  --auth-text-color: #f6f6f680;
  --text-color: #ffffff80;
  --amazon-filter-img: brightness(.1) invert(.5);
  --filter-filter-wImg: brightness(.1) invert(.5);
  --filter-apple-img: grayscale(99%);
  --filter-apple-wImg: grayscale(99%);
  --filter-img: brightness(.1) invert(.5);
  --filter-wImg: brightness(.1) invert(.5);
  --amazon-hover: brightness(.1) invert(1);
  --amazon-hover: brightness(.1) invert(1);
  --scroll-color: #1119;
}

@font-face {
  font-family: italic;
  src: url("DMSans-Italic-VariableFont_opsz,wght.9f863b5a.ttf");
  font-display: swap;
}

@font-face {
  font-family: regular;
  src: url("DMSans-VariableFont_opsz,wght.cac90019.ttf");
  font-display: swap;
}

body {
  background-color: var(--primery-bgc);
  scrollbar-width: thin;
  font-family: regular;
  font-weight: 400;
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-thumb {
  background-color: var(--scroll-color);
  border-radius: 12px;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
}

address {
  font-style: normal;
}

._list {
  list-style: none;
}

._link {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  flex-direction: row;
  display: flex;
}

body {
  margin: 0;
  display: block;
}

._section {
  padding-top: 40px;
  padding-bottom: 100px;
}

._container {
  max-width: 375px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  ._container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  ._container {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.disable-scroll {
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.header {
  z-index: 5;
  display: block;
  position: sticky;
  top: 0;
}

.header ._container {
  border: 1.5px solid;
  border-color: var(--color-black);
  background-color: var(--color-white);
  height: 64px;
  max-width: 768px;
  width: auto;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  padding-top: 14px;
  padding-bottom: 14px;
  display: flex;
}

.switch__input:focus + .switch__slider {
  outline: 1px solid var(--color-violet);
  cursor: pointer;
}

.switch__input:hover + .switch__slider {
  cursor: pointer;
}

.logo-box {
  width: 108px;
  height: 28px;
  display: flex;
}

.logo-box-link {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo-text {
  width: 77px;
  height: 14px;
}

.for_desktop.hidden, .for_desktop, .hidden, .user.hidden {
  display: none;
}

.header-logo {
  width: 24px;
  height: 24px;
}

.logo-text {
  fill: var(--color-black);
}

.header-box {
  width: 82px;
  height: 28px;
  justify-content: space-between;
  align-items: center;
  gap: 14px;
  display: flex;
}

.theme-switcher {
  background-image: linear-gradient(to bottom, var(--color-violet), var(--color-white) );
  border-radius: 18px;
  display: inline-block;
  position: relative;
}

.switch__input {
  width: 0;
  height: 0;
  opacity: 0;
  appearance: none;
  position: absolute;
}

.switch__slider {
  display: inline-block;
  position: relative;
}

.switch__slider:before {
  content: "";
  background-image: linear-gradient(to bottom, var(--color-white), #f7f7f7);
  width: 18px;
  height: 18px;
  border-radius: 50%;
  transition: all .1s;
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switch__slider {
  content: "";
  width: 40px;
  height: 20px;
  background-image: linear-gradient(to bottom, var(--color-violet), var(--color-white) );
  border-radius: 18px;
  display: block;
}

.dark-theme .switch__slider:before {
  transform: translateX(20px);
}

.qq .switch__slider:before {
  transform: translateY(0);
}

.switch__input:checked + .switch__slider {
  background-image: var(--color-switcher);
}

.header-menu {
  width: 28px;
  height: 28px;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.header-menu svg path {
  transition: all .5s;
}

.header-menu:is(:hover, :focus) svg path {
  stroke: var(--color-violet);
}

.user, .js-menu-container {
  display: none;
}

.is-open {
  z-index: 10;
  height: calc(100vh - 64px);
  width: 100vw;
  background-image: radial-gradient(189.32% 190.93% at 59.76% -23.42%, var(--color-violet) 18.03%, var(--color-white) 100%);
  flex-flow: column wrap;
  padding: 20px;
  display: flex;
  position: fixed;
  overflow: hidden;
}

.menu-user {
  width: auto;
  height: 37px;
  background-color: #0000;
  border: none;
  justify-content: space-between;
  align-self: start;
  align-items: center;
  gap: 9px;
  padding: 4px 0;
  display: flex;
}

.menu-user.hidden {
  display: none;
}

.menu-sign-up-text {
  color: var(--color-white);
}

.user-name {
  white-space: nowrap;
  text-overflow: ellipsis;
  color: var(--color-white);
  letter-spacing: -.44px;
  margin-left: 5px;
  font-family: inherit;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  overflow: hidden;
}

.arrow-down {
  fill: var(--color-white);
  margin-left: 4px;
  display: block;
}

.arrow-down .hidden {
  display: none;
}

.thumb-user-icon {
  width: 37px;
  height: 37px;
  background-color: var(--color-white);
  border-radius: 50%;
  overflow: hidden;
}

.menu-user-name {
  color: var(--color-white);
  letter-spacing: -.44px;
  width: auto;
  font-family: inherit;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.menu-header-nav {
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 130px;
  display: flex;
}

.menu-home.active, .menu-shopping-list.active {
  background-color: var(--color-yellow);
  border-radius: 36px;
  font-weight: 700;
}

.menu-home, .menu-shopping-list {
  width: auto;
  height: auto;
  color: #fff;
  letter-spacing: -.56px;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  gap: 4px;
  padding: 8px 16px;
  font-family: inherit;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.29;
  display: flex;
}

.menu-home ~ svg, .menu-shopping-list ~ svg {
  fill: #fff;
}

.menu-shopping-list svg path {
  fill: var(--color-white);
}

.menu-home:is(:hover, :focus), .menu-shopping-list:is(:hover, :focus) {
  background-color: var(--color-yellow);
  color: var(--color-black);
  border-radius: 36px;
  font-weight: 700;
}

.menu-shopping-list:is(:hover, :focus) svg path {
  fill: var(--color-black);
}

.books {
  z-index: -1;
  width: 450px;
  height: 439px;
  background: url("bg-books.bef57781.png") no-repeat;
  flex-shrink: 0;
  position: absolute;
  bottom: -50%;
  left: 50%;
  transform: rotate(-15deg)translate(-50%, -50%);
}

.js_log_out {
  width: 118px;
  height: 48px;
  border: 1.5px solid var(--color-black);
  background: var(--color-white);
  color: var(--color-black);
  letter-spacing: -.56px;
  border-radius: 18px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  padding: 14px 24px;
  font-family: inherit;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.29px;
  position: fixed;
  bottom: 20px;
  left: 20px;
}

@media screen and (min-width: 768px) {
  .header ._container {
    max-width: 768px;
  }

  .logo-nav-wrapper {
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    display: flex;
  }

