  .home:is(:hover, :focus), .shopping-list:is(:hover, :focus) {
    background-color: var(--color-yellow);
    border-radius: 36px;
    font-weight: 700;
  }

  .header-box {
    width: auto;
    height: auto;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    display: flex;
  }

  .header-menu {
    display: none;
  }

  .user {
    width: fit-content;
    max-width: 230px;
    height: 45px;
    background-color: var(--color-violet);
    border: none;
    border-radius: 36px;
    justify-content: space-between;
    align-items: center;
    gap: 4px;
    padding: 14px;
