  }

  .for_desktop {
    display: flex;
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
  }

  .sign-up-text {
    width: 46px;
    height: 18px;
    color: var(--color-white);
    letter-spacing: -.56px;
    font-family: inherit;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.29;
    display: block;
  }
}

@media screen and (min-width: 1440px) {
