  height: 64px;
  cursor: pointer;
  z-index: 2;
  border: 5px solid #4f2ee84d;
  border-radius: 50%;
  flex-shrink: 0;
  position: fixed;
  top: 95%;
  right: 20px;
  transform: translateY(-50%);
}

.go-top:hover {
  box-shadow: 1px 1px 25px 10px #9294f866;
}

.icon-scroll-up-wrap {
  background-color: var(--color-violet);
  width: 100%;
  height: 100%;
  opacity: .9;
  border-radius: 50%;
}

.icon-scroll-up {
  margin-top: 18px;
  margin-left: 18px;
}

