  flex-flow: column wrap;
  justify-content: center;
  gap: 40px;
  margin-top: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .best_list.category-list {
    flex-direction: row;
    gap: 40px 25px;
  }
}

@media screen and (min-width: 1440px) {
  .best_list.category-list {
    column-gap: 24px;
  }
